"use client";

import AdminHeader from "@/components/admin-dashboard/AdminHeader"; 
import StatsOverview from "@/components/admin-dashboard/StatsOverview";
import AttendanceManager from "@/components/admin-dashboard/AttendanceManager";
import StudentDirectory from "@/components/admin-dashboard/StudentDirectory";
import { adminData } from "@/lib/adminData";
import { useState } from "react";
import { LayoutGrid, Users } from "lucide-react";

export default function AdminDashboard() {
  const [activeTab, setActiveTab] = useState<"attendance" | "directory">("attendance");

  return (
    <div className="min-h-screen bg-slate-50 selection:bg-indigo-100">
      <div className="absolute top-0 left-0 right-0 h-80 bg-gradient-to-b from-white to-slate-50 pointer-events-none" />
      
      <AdminHeader admin={adminData.admin} />

      <main className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-10">
        
        <StatsOverview stats={adminData.stats} />

        <div className="mb-8">
          <div className="inline-flex p-1 bg-white border border-gray-200 rounded-xl shadow-sm">
            <button
              onClick={() => setActiveTab("attendance")}
              className={`flex items-center gap-2 px-6 py-2.5 rounded-lg text-sm font-bold transition-all duration-200 ${
                activeTab === "attendance"
                  ? "bg-indigo-600 text-white shadow-md shadow-indigo-200"
                  : "text-slate-500 hover:text-slate-900 hover:bg-slate-50"
              }`}
            >
              <LayoutGrid className="w-4 h-4" />
              Attendance
            </button>
            <button
              onClick={() => setActiveTab("directory")}
              className={`flex items-center gap-2 px-6 py-2.5 rounded-lg text-sm font-bold transition-all duration-200 ${
                activeTab === "directory"
                  ? "bg-indigo-600 text-white shadow-md shadow-indigo-200"
                  : "text-slate-500 hover:text-slate-900 hover:bg-slate-50"
              }`}
            >
              <Users className="w-4 h-4" />
              Directory
            </button>
          </div>
        </div>

        <div className="animate-in fade-in slide-in-from-bottom-4 duration-500">
          {activeTab === "attendance" ? (
            <AttendanceManager students={adminData.students} />
          ) : (
            <StudentDirectory students={adminData.students} />
          )}
        </div>

      </main>
    </div>
  );
}